___CSS_LOADER_EXPORT___.push([module.id, ".error{color:red}", "",{"version":3,"sources":["webpack://./resources/js/components/App.vue"],"names":[],"mappings":"AAuEA,OACA,SACA","sourcesContent":["<template>\n    <div class=\"container\">\n        <div class=\"row my-4\">\n            <h1 class=\"offset-sm-3\"> </h1>\n        </div>\n        <div>\n            <div class=\"row mb-3\">\n                <div class=\"col-sm-3 offset-sm-3\">\n                    <label for=\"name\" class=\"form-label\"></label>\n                    <input type=\"text\" class=\"form-control\" id=\"name\" placeholder=\"\" v-model=\"message.name\">\n                </div>\n                <div class=\"col-sm-3\">\n                    <label for=\"phone\" class=\"form-label\"></label>\n                    <input type=\"text\" class=\"form-control\" id=\"phone\" placeholder=\"\" v-model=\"message.phone\">\n                </div>\n            </div>\n            <div class=\"row mb-4\">\n                <div class=\"col-sm-6 offset-sm-3\">\n                    <label for=\"text\" class=\"form-label\"></label>\n                    <textarea class=\"form-control\" id=\"text\" rows=\"5\" v-model=\"message.text\"></textarea>\n                </div>\n            </div>\n            <div class=\"row\">\n                <div class=\"col-sm-1 offset-sm-3\">\n                    <button class=\"btn btn-primary mb-3\" @click=\"store\"></button>\n                </div>\n                <div class=\"col-sm-5 ms-3\">\n                    <div class=\"error\" v-if=\"errors.length\" v-html=\"errors\"></div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    name: \"App\",\n    data: () => ({\n        message: {\n            name: '',\n            phone: '',\n            text: '',\n        },\n        errors: []\n    }),\n    methods: {\n        async store() {\n            const res = await fetch('api/messages', {\n                method: 'POST',\n                headers: {\n                    'Content-Type': 'application/json;charset=utf-8',\n                },\n                body: JSON.stringify(this.message),\n            });\n\n            const data = await res.json();\n\n            if (res.status === 422) {\n                this.errors = Object.values(data).map(item => item.join('<br>')).join('<br>');\n            } else {\n                this.message.name = '';\n                this.message.phone = '';\n                this.message.text = '';\n\n                this.errors = '';\n            }\n        },\n    }\n}\n</script>\n<style>\n    .error {\n        color: red;\n    }\n</style>\n"],"sourceRoot":""}]);
